@import url(https://fonts.googleapis.com/css?family=Yellowtail&display=swap);

html,body{height:100%;font-size:62.5%;font-family:"Yellowtail";color:#222222}.container{display:flex;flex-direction:column;align-items:center;width:100vw;height:100vh}.title{font-weight:bold;font-size:calc(2vw + 2vh);padding:calc(2vw + 1vh);margin:auto}@-webkit-keyframes blink{0%{opacity:0.5}30%{opacity:1}70%{opacity:1}100%{opacity:0.5}}@keyframes blink{0%{opacity:0.5}30%{opacity:1}70%{opacity:1}100%{opacity:0.5}}.term{color:#222222;font-size:calc(3vw + 1.5vh)}.button__start{font-weight:700;font-size:calc(2vw + 1vh);color:#ccc;text-transform:uppercase;text-decoration:none;background:white;margin-top:2rem;padding:2rem;border:none;outline:none}.button__start:hover{color:#494949}.input{font-family:"Yellowtail";border:none;border-bottom:0.3rem solid #ccc;font-size:calc(2vw + 2vh);margin:1rem;outline:none;text-align:center}.input::-webkit-input-placeholder{color:#ccc}.input:-ms-input-placeholder{color:#ccc}.input::-ms-input-placeholder{color:#ccc}.input::placeholder{color:#ccc}.input__title{width:calc(25vw + 25vh)}.input__minute{width:calc(12vw + 12vh)}.input__minute::-webkit-inner-spin-button,.input__minute::-webkit-outer-spin-button{-webkit-appearance:none;margin:0}

.timer__container{width:calc(15rem + 20vw + 10vh);height:calc(15rem + 20vw + 10vh);margin:5vh}.timer__common{cx:50;cy:50;-webkit-transform:rotate(-90deg);transform:rotate(-90deg);-webkit-transform-origin:50% 50%;transform-origin:50% 50%;fill:transparent}.timer__main{r:20;stroke:orangered;stroke-width:40;stroke-dasharray:125.66371 125.66371}.timer__scale{stroke:grey}.timer__scale--1min{stroke-width:2;r:45}.timer__scale--10min{stroke-width:4;r:45}.finishMessage{font-weight:bold;font-size:calc(4vw + 2vh);width:100%;height:100%;display:flex;align-items:center;justify-content:center}

