@import url("https://fonts.googleapis.com/css?family=Yellowtail&display=swap");

$main_font_size: calc(2vw + 2vh);
html,
body {
  height: 100%;
  font-size: 62.5%;
  font-family: "Yellowtail";
  color: #222222;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
.title {
  font-weight: bold;
  font-size: $main_font_size;
  padding: calc(2vw + 1vh);
  margin: auto;

  @keyframes blink {
    0% {
      opacity: 0.5;
    }
    30% {
      opacity: 1;
    }
    70% {
      opacity: 1;
    }
    100% {
      opacity: 0.5;
    }
  }
}
.term {
  color: #222222;
  font-size: calc(3vw + 1.5vh);
}

.button {
  &__start {
    font-weight: 700;
    font-size: calc(2vw + 1vh);
    color: #ccc;
    text-transform: uppercase;
    text-decoration: none;
    background: white;
    margin-top: 2rem;
    padding: 2rem;
    border: none;
    outline: none;
    &:hover {
      color: #494949;
    }
  }
}
.input {
  font-family: "Yellowtail";
  border: none;
  border-bottom: 0.3rem solid #ccc;
  font-size: $main_font_size;
  margin: 1rem;
  outline: none;
  text-align: center;
  &::placeholder {
    color: #ccc;
  }
  &__title {
    width: calc(25vw + 25vh);
  }
  &__minute {
    width: calc(12vw + 12vh);
    &::-webkit-inner-spin-button,
    &::-webkit-outer-spin-button {
      -webkit-appearance: none;
      margin: 0;
    }
  }
}
