$pi: 3.14159265358979;
$minute: 20;
$main_r: 20;
$sub_r: 45;
$main_c: $main_r * 2 * $pi;
$sub_c: $sub_r * 2 * $pi;
:export {
  mainCir: $main_c;
  subCir: $sub_c;
}

.timer {
  &__container {
    width: calc(15rem + 20vw + 10vh);
    height: calc(15rem + 20vw + 10vh);
    margin: 5vh;
  }
  &__common {
    cx: 50;
    cy: 50;
    transform: rotate(-90deg);
    transform-origin: 50% 50%;
    fill: transparent;
  }
  &__main {
    r: $main_r;
    stroke: orangered;
    stroke-width: $main_r * 2;
    stroke-dasharray: $main_c $main_c;
  }
  &__scale {
    stroke: grey;
    &--1min {
      stroke-width: 2;
      r: $sub_r;
    }
    &--10min {
      stroke-width: 4;
      r: $sub_r;
    }
  }
}

.finishMessage {
  font-weight: bold;
  font-size: calc(4vw + 2vh);
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
